import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path: '*',
            redirect: '/index'
        },
        {
            path: '/home',
            name: 'home',
            component: resolve=>(require(["@/components/Master.vue"],resolve)),
            redirect:'/index',
            children:[
                {
                    path: '/index',
                    name: 'index',
                    component: resolve=>(require(["@/components/deveGuide/Index.vue"],resolve))
                },
                {
                    path: '/quickStart',
                    name: 'quickStart',
                    component: resolve=>(require(["@/components/deveGuide/QuickStart.vue"],resolve))
                },
                {
                    path: '/codeStandard',
                    name: 'codeStandard',
                    component: resolve=>(require(["@/components/deveGuide/CodeStandard.vue"],resolve))
                },
                {
                    path: '/icon',
                    name: 'icon',
                    component: resolve=>(require(["@/components/base/Icon.vue"],resolve))
                },
                {
                    path: '/button',
                    name: 'button',
                    component: resolve=>(require(["@/components/base/Button.vue"],resolve))
                },
                {
                    path: '/commonCode',
                    name: 'commonCode',
                    component: resolve=>(require(["@/components/others/CommonCode.vue"],resolve))
                },
                {
                    path: '/txtLinkHref',
                    name: 'txtLinkHref',
                    component: resolve=>(require(["@/components/base/TxtLinkHref.vue"],resolve))
                },
                {
                    path: '/radio',
                    name: 'radio',
                    component: resolve=>(require(["@/components/base/Radio.vue"],resolve))
                },
                {
                    path: '/checkbox',
                    name: 'checkbox',
                    component: resolve=>(require(["@/components/base/Checkbox.vue"],resolve))
                },
                {
                    path: '/input',
                    name: 'input',
                    component: resolve=>(require(["@/components/base/Input.vue"],resolve))
                },
                {
                    path: '/countNumber',
                    name: 'countNumber',
                    component: resolve=>(require(["@/components/base/CountNumber.vue"],resolve))
                },
                {
                    path: '/table',
                    name: 'table',
                    component: resolve=>(require(["@/components/form/Table.vue"],resolve))
                },
                {
                    path: '/badge',
                    name: 'badge',
                    component: resolve=>(require(["@/components/base/Badge.vue"],resolve))
                },
                {
                    path: '/breadcrumb',
                    name: 'breadcrumb',
                    component: resolve=>(require(["@/components/base/Breadcrumb.vue"],resolve))
                },
                {
                    path: '/addressList',
                    name: 'addressList',
                    component: resolve=>(require(["@/components/business/AddressList.vue"],resolve))
                },
                {
                    path: '/goodsCard',
                    name: 'goodsCard',
                    component: resolve=>(require(["@/components/business/GoodsCard.vue"],resolve))
                },
                {
                    path: '/coupon',
                    name: 'coupon',
                    component: resolve=>(require(["@/components/business/Coupon.vue"],resolve))
                },
            ]
        },

    ]
})



